
'use server';

/**
 * @fileOverview A flow to generate a video presentation for the app.
 */

import { ai } from '@/ai/genkit';
import { googleAI } from '@genkit-ai/googleai';
import { z } from 'genkit';
import { type MediaPart } from 'genkit';

const VideoPresentationOutputSchema = z.object({
  videoDataUri: z.string().optional().describe('The generated video as a data URI.'),
});
export type VideoPresentationOutput = z.infer<typeof VideoPresentationOutputSchema>;

const videoGenerationPrompt = `
Create a dynamic and engaging 15-second promotional video for 'My Abacus Pro', a web app designed to make learning mental math fun and effective. The video should have an upbeat, modern, and educational tone with smooth transitions and a consistent, clean aesthetic.

**Scene 1: Opening Title (3 seconds)**
- **Visual:** An eye-catching animation where a stylized brain logo forms on screen, followed by the text "My Abacus Pro" fading in elegantly below it. The background should be a clean, bright gradient (e.g., light blue to white).
- **Text Overlay:** My Abacus Pro
- **Music:** Upbeat, inspiring, and modern electronic track begins and plays throughout.

**Scene 2: Timed Math Challenges (4 seconds)**
- **Visual:** A quick, dynamic montage showing different math problems (e.g., "45 + 98", "12 x 8", "153 - 76") appearing on screen. A ticking clock timer should be visible, creating a sense of urgency and excitement. Show numbers rapidly changing as if being calculated.
- **Text Overlay (Pops up sequentially):** Timed Challenges, Speed & Accuracy
- **Sound Effect:** Subtle "tick-tock" sound under the music.

**Scene 3: Interactive Learning Tools (4 seconds)**
- **Visual:** Transition to a clean shot of the app's interactive abacus tool. Show beads moving smoothly to solve a problem. Then, cut to a shot of the bubble popping game, with colorful bubbles containing numbers being popped.
- **Text Overlay:** Interactive Tools, Fun & Engaging
- **Sound Effect:** A satisfying "click" for the abacus beads and a gentle "pop" for the bubbles.

**Scene 4: Track Your Progress (2 seconds)**
- **Visual:** A sleek animation of a progress chart (like a line graph) trending upwards, with key stats like "95% Accuracy" and "2x Faster" highlighted.
- **Text Overlay:** Track Your Progress

**Scene 5: Call to Action (2 seconds)**
- **Visual:** The video ends on a clean screen with the "My Abacus Pro" logo and a clear call-to-action button.
- **Text Overlay:** Start Learning Today!
- **Music:** The music track hits a final, positive crescendo and fades out.
`;

const videoPresentationFlow = ai.defineFlow(
  {
    name: 'videoPresentationFlow',
    inputSchema: z.void(),
    outputSchema: VideoPresentationOutputSchema,
  },
  async () => {
    let operation = await ai.generate({
      model: googleAI.model('veo-2.0-generate-001'),
      prompt: videoGenerationPrompt,
      config: {
        durationSeconds: 15,
        aspectRatio: '16:9',
      },
    });

    if (!operation || !operation.operation) {
      throw new Error('Expected the model to return an operation');
    }

    // Wait until the operation completes.
    while (!operation.operation.done) {
      // Wait for 5 seconds before checking again.
      await new Promise((resolve) => setTimeout(resolve, 5000));
      operation = await ai.checkOperation(operation.operation);
    }

    if (operation.operation.error) {
      throw new Error('Failed to generate video: ' + operation.operation.error.message);
    }
    
    const videoPart = operation.operation.output?.message?.content.find(
      (p: MediaPart) => p.media?.contentType === 'video/mp4'
    );

    if (!videoPart || !videoPart.media) {
       return { videoDataUri: undefined };
    }

    const fetch = (await import('node-fetch')).default;
    // Add API key before fetching the video.
    const videoDownloadResponse = await fetch(
      `${videoPart.media.url}&key=${process.env.GEMINI_API_KEY}`
    );
    
    if (!videoDownloadResponse.ok || !videoDownloadResponse.body) {
        throw new Error(`Failed to fetch video: ${videoDownloadResponse.statusText}`);
    }

    const videoBuffer = await videoDownloadResponse.arrayBuffer();
    const base64Video = Buffer.from(videoBuffer).toString('base64');
    
    return {
      videoDataUri: `data:video/mp4;base64,${base64Video}`,
    };
  }
);

export async function generateVideoPresentation(): Promise<VideoPresentationOutput> {
    return videoPresentationFlow();
}
